<template>
  <div class="translation-tool">
    <h3>翻译工具</h3>
    <div class="input-section">
      <textarea v-model="sourceText" placeholder="输入要翻译的文本..."></textarea>
      <select v-model="targetLanguage">
        <option value="en">英语</option>
        <option value="zh">中文</option>
        <option value="es">西班牙语</option>
        <!-- 可添加更多语言选项 -->
      </select>
      <button @click="translateText">翻译</button>
    </div>
    <div class="output-section" v-if="translatedText">
      <h4>翻译结果：</h4>
      <p>{{ translatedText }}</p>
    </div>
  </div>
</template>

<script>

import axios from 'axios';

export default {
  name: 'TranslationTool',
  data() {
    return {
      sourceText: '',
      targetLanguage: 'en',
      translatedText: ''
    };
  },
  methods: {
    async translateText() {
      if (!this.sourceText.trim()) {
        alert('请输入要翻译的文本！');
        return;
      }
      const requestBody = {
          query: "今天吃什么？",
          from: "zh",
          to: "en",
          reference: "",
          corpusIds: [],
          needPhonetic: true,
          domain: "common",
          milliTimestamp: Date.now() 
      }

      const requestHeaders = {
        //   'Content-Type': 'application/json',     // 设置内容类型
          'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 ', // 设置 User-Agent
          'Origin': 'https://fanyi.baidu.com',
          'referer': 'https://fanyi.baidu.com'
      }
      try {
        // 示例翻译功能（可替换为实际 API 请求）
        const response = await axios.post('https://fanyi.baidu.com/ait/text/translate', requestBody, { headers: requestHeaders });
        console.log(response.data);
        // this.translatedText = `翻译为 ${this.targetLanguage}：${this.sourceText}`;
      } catch (error) {
        console.error('翻译失败:', error);
        alert('翻译失败，请稍后再试！');
      }
    }
  }
};
</script>

<style scoped>
.translation-tool {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}

.input-section {
  margin-bottom: 20px;
}

textarea {
  width: 100%;
  height: 100px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

select {
  margin-right: 10px;
  padding: 5px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  background-color: #3498db;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2980b9;
}

.output-section {
  margin-top: 20px;
  text-align: left;
}

h4 {
  color: #2c3e50;
}
</style>